.btnContainer {

  .btn {
    margin: 10px;
    border-radius: 15px;
    padding: 10px 20px;
    cursor: pointer;
  }

  .btn:hover {
    opacity: 1;
  }

  .nonActiveBtn {
    opacity: 0.7;
  }

  .activeBtn {
    opacity: 1;
  }

  .primaryBtn {
    background: rgb(17, 66, 82);
    border: 1px solid lightblue;
  }

  .successBtn {
    background: green;
    border: 1px solid green;
  }

  .errorBtn {
    background: red;
    border: 1px solid red;
  }

  .infoBtn {
    background: orange;
    border: 1px solid orange;
  }

  .secondaryBtn {
    background: lightgray;
    border: 1px solid lightgray;
  }

}